import { AchievementService } from './services/achievement.service';
import { ToastController } from '@ionic/angular';
import { Component, ViewChild, OnInit } from '@angular/core';
import { MenuItems, menuItemsToken, trackingInfo } from './common';
import { ItemReorderEventDetail } from '@ionic/core';
import { Location } from '@angular/common';
import { App } from '@capacitor/app';


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit {

  @ViewChild('reorderGroup') reorderGroup: any;

  showDragMeInfoOnlyOnce = true;
  showDragMeInfo = false;
  currentRoute;

  menuItems: MenuItems[] = MenuItems;

  constructor(
    private toastController: ToastController,
    private location: Location,
    private achievementService: AchievementService
    ) {
      this.backButtonEv();
      const menuItemsFromStorage = JSON.parse(localStorage.getItem(menuItemsToken));
      if(!!menuItemsFromStorage) {
        this.menuItems = menuItemsFromStorage;
      }
  }

  ngOnInit(): void {
      window['ga-disable-UA-G-5L53KZNJMV-Y'] = true;
      var _gaq = _gaq || [];
      _gaq.push(['_setAccount', 'UA-G-5L53KZNJMV-Y']);
      _gaq.push(['_trackPageview']);

      this.achievementService.getInitialAchievementList();
      if(!localStorage.getItem(trackingInfo)) {
        this.presentCookieToast();
      } else if (localStorage.getItem(trackingInfo) === 'true') {

      }
  }

  async presentCookieToast() {
    const toast = await this.toastController.create({
      message: `This site uses cookies / local storage to track user's activity via Google Analytics. \n`,
      position: 'bottom',
      color: 'primary',
      buttons: [       
        {
          side: 'end',
          icon: 'checkmark',
          text: 'Ok',
          handler: () => {
            window['ga-disable-UA-G-5L53KZNJMV-Y'] = false;
            // localStorage.setItem(trackingInfo, 'true');
            var _gaq = _gaq || [];
            //...snip...
            //set a cookie to express that the user has opted-in to tracking, for future pageviews
            _gaq.push(['_trackPageview']); // now run the pageview that you 'missed'
          },
        },
        {
          side: 'start',
          icon: 'close',
          text: 'Decline',
          handler: () => {
            localStorage.setItem(trackingInfo, 'false');
            window['ga-disable-UA-G-5L53KZNJMV-Y'] = true;
          }
        }
    ]
    });
    toast.present();
  }

  backButtonEv() {
    App.addListener('backButton', () =>
    {
      if (this.location.isCurrentPathEqualTo('/home'))
      {
        if (window.confirm("Are you sure you want to exit the app?")){
          App.exitApp();
        }
      } 
      else
      {
        this.location.back();
      }
    });
  }

  reorderItems(event: any): void {
    this.showDragMeInfo = false;
    const itemMove = this.menuItems.splice(event.detail.from, 1)[0];
    this.menuItems.splice(event.detail.to, 0, itemMove);
    localStorage.setItem(menuItemsToken, JSON.stringify(this.menuItems));
    event.detail.complete();

    if(this.menuItems[this.menuItems.length - 1].name === 'Home') {
      this.presentToast();
    }
  }

  async presentToast() {
    const toast = await this.toastController.create({
      message: `Seriously?! Who put's 'Home' at the bottom of the list?`,
      duration: 4000,
      position: 'middle',
      icon: 'warning',
      color: 'warning'
    });
    toast.present();
  }

  toggleReorderGroup(): void {
    this.reorderGroup.disabled = !this.reorderGroup.disabled;

    if(this.showDragMeInfoOnlyOnce) {
      this.showDragMeInfo = true;
      this.showDragMeInfoOnlyOnce = false;
    }
  }
}
